<template>
<TabContent style="overflow-y: auto; max-height: 100%">
  <v-text-field style="border-radius: 0px" filled placeholder="Фильтр" v-model="filter" hide-details/>
  <v-container>
  <v-row class="ticket-list">
    <TicketItem v-for="t in sorted" :key="t.id" :value="t"/>
  </v-row>
  </v-container>
</TabContent>
</template>

<style scoped>
desktop .ticket-list {
  padding: 10px;
}
</style>

<script>
import TabContent from '@/render/components/Common/TabContent.vue';
import TicketItem from '@/render/components/Tickets/TicketItem.vue';

export default {
  props: ['value'],
  components: { TabContent, TicketItem },
  data: () => ({
    filter: ''
  }),

  mounted() {
    this.updateTitle();
  },

  methods: {
    updateTitle() {
      this.value.title = `Тикеты [${this.list.length}/${this.list.filter(v => v.unread).length}]`;
      this.value.fullTitle = this.value.title;
    }
  },

  computed: {
    list() {
      return this.$store.state.ticket.list;
    },

    filtered() {
      const filter = this.filter && this.filter.toLowerCase();
      const check = data => data && data.toString().toLowerCase().includes(filter);
      return filter ? this.list.filter(
        v => check(v.name) || check(v.client) || check(v.location) || check(v.ticket) || check(v.responsible)
      ) : this.list;
    },

    sorted() {
      return this.filtered.map(b => {
        if (b.blocked_by_me) {
          b.priority = 3;
          return b;
        }

        if (b.unread) {
          b.priority = 2;
          return b;
        }

        if (b.blocked_by) {
          b.priority = 0;
          return b;
        }

        b.priority = 1;
        return b;
      }).sort((a, b) => b.priority - a.priority);
    }
  },

  watch: {
    list() {
      this.updateTitle();
    }
  }
};
</script>
