<template>
<v-col cols="12" sm="6" class="ticket-item">
<v-card dense :style="`border-left: ${border};`" @click="showWindow" :tile="core.mobile">
<v-list-item dense two-line>
  <v-badge
          :color="this.badge ? 'accent' : 'rgba(0,0,0,0)'"
          overlap
          :content="this.badge"
          bottom offset-x="30" offset-y="32"
        ><v-list-item-avatar color="grey">
        <img :src="avatarCache.getAvatar(value.client)"/>
  </v-list-item-avatar></v-badge>
  <v-list-item-content>
    <v-list-item-title v-text="value.name"/>
    <v-list-item-subtitle>{{ value.client }}</v-list-item-subtitle>
    <v-list-item-subtitle>{{ value.responsible }}<span v-if="!value.blockedByMe && value.blockedBy"> 🔒 {{ value.blockedBy }}</span></v-list-item-subtitle>
  </v-list-item-content>
  <v-list-item-action>
    <v-list-item-subtitle>{{ createdAt }}</v-list-item-subtitle>
    <v-list-item-subtitle>{{ value.ticket }}</v-list-item-subtitle>
    <v-list-item-subtitle>{{ value.location }}</v-list-item-subtitle>
  </v-list-item-action>
</v-list-item>
</v-card>
</v-col>
</template>

<style scoped>
.mobile .ticket-item {
  padding: 0px;
}
</style>

<script>
import moment from 'moment';

moment.locale('ru');

export default {
  props: ['value'],

  computed: {
    createdAt() {
      const [_, days, hours, minutes] = this.value.delay.match(/(\d+)d\+(\d+):(\d+)/);
      const createdAt = (Date.now() / 1000) - days * 86400 - hours * 3600 - minutes * 60;
      return moment.unix(createdAt).fromNow();
    },

    border() {
      if (this.value.unread) {
        const [_, days, hours, minutes] = this.value.delay.match(/(\d+)d\+(\d+):(\d+)/);
        const delay = days * 60 * 24 + hours * 60 + minutes * 1;
        const color = delay >= 15 ? '#ff3b00' : '#ff9c2a';
        return `3px solid ${color}`;
      }

      if (this.value.blocked_by_me === 'on') {
        return '3px solid #2aabff';
      }

      if (this.value.blocked_by) {
        return '3px solid #7b0202';
      }

      return '0px';
    },

    dublicate() {
      return !!this.$store.state.ticket.list.find(v => v.client === this.value.client && v !== this.value);
    },

    badge() {
      return `${['', '💖', '💔'][this.value.attitude]}${this.dublicate ? '🐀' : ''}`;
    }
  },

  methods: {
    async showWindow() {
      this.tabMgr.open('Ticket', { ticketId: this.value.ticket, elId: this.value.id });
    }
  }
};
</script>
